* {
  margin: 0;
}

.App-header {
  background-color: #282c34;
  height: 5rem;
  display: flex;
  color: white;
  justify-content: center;
  align-items: center;
  position: sticky;
  top: 0;
}

.App-logo {
  height: 100%;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-main {
  text-align: center;
  display: flexbox;
  width: 100%;
  align-content: space-around;
  align-items: center;
}

.App-button {
  border: none;
  background-color: #282c34;
  color: #61dafb;
}

.App-input {
  outline: none;
  border: none;
}

.App-input:focus {
  outline: none;
  border: none;
}

.App-output {
  min-height: 20%;
  height: 20%;
}

.App-item {
  min-width: 90%;
  width: 90%;
  margin: 10px;
  text-align: center;
  min-height: 2rem;
  border: 0;
}

.App-selectable {
  min-width: 90%;
  width: 90%;
  margin: 10px;
  text-align: center;
  min-height: 2rem;
}

.App-string-container {
  margin: 20px;
}

.App-string-container > * {
  margin: 0;
}

.App-lang-container > * {
  margin: 0;
}
